<script>
export default {
  data () {
    return {
      headShow: false,
      headIndex: 0,
      contentShow: false,
      footerShow: false,
      modalShow: false,
      labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
      dates: ['3/21-4/19', '4/20-5/20', '5/21-6/21', '6/22-7/22', '7/23-8/22', '8/23-9/22', '9/23-10/23', '10/24-11/22', '11/23-12/21', '12/22-1/19', '1/20-2/18', '2/19-3/20'],
      horoscope: this.$route.params.horoscope,
      info: '',
      number: 7,
      colors: ['#ccaf40', '#c83838', '#3a93e3', '#3eb58a', '#7746b7', '#0a0a0a', '#f5f5f5', '#cf8e2c', '#c35840', '#77b436'],
      colours: ['', '', '', '', '', '', '', '', '', ''],
      color: 0,
      stars: [3, 4, 2, 3, 2], //     
      week: '3/21-3/27',
      weekstars: [4, 3, 4, 3, 5], //     
      month: '3',
      monthstars: [2, 3, 1, 2, 3], //     
      modalIndex: 0,
      modalTitle: '',
      modalText: '',
      modalTextWeek: '',
      modalTextMonth: ''
    }
  },
  computed: {
    date () {
      return this.dates[this.horoscope]
    },
    label () {
      return this.labels[this.horoscope]
    }
  },
  ready () {
    this.$set('headShow', true)
    this.$set('contentShow', true)
    this.$set('footerShow', true)

    Draggable.create('#scroller', {
      type: 'scrollTop',
      edgeResistance: 1,
      throwProps: true,
      dragClickables: true,
      lockAxis: true
    })
  },
  methods: {
    headTouchLeft () {
      this.$set('headIndex', 1)
    },
    headTouchMiddle () {
      this.$set('headIndex', 0)
    },
    headTouchRight () {
      this.$set('headIndex', 2)
    },
    contentLeft () {
      switch (this.headIndex) {
        case 1:
          this.$set('headIndex', 0)
          break
        case 0:
          this.$set('headIndex', 2)
          break
        default:
      }
    },
    contentRight () {
      switch (this.headIndex) {
        case 2:
          this.$set('headIndex', 0)
          break
        case 0:
          this.$set('headIndex', 1)
          break
        default:
      }
    },
    contentUp () {
      this.$set('footerShow', false)
    },
    contentDown () {
      this.$set('footerShow', true)
    },
    showModal (index, label) {
      this.$set('modalIndex', index)
      this.$set('modalTitle', label)
      this.$set('modalShow', true)
      Draggable.create('#modalScroller', {
        type: 'scrollTop',
        edgeResistance: 1,
        throwProps: true,
        dragClickables: true,
        lockAxis: true
      })
    },
    hideModal () {
      this.$set('modalShow', false)
    }
  }
}
</script>

