<template>
  <div class="head head-c" v-show="headShow" v-bind:class="{ 'head-1': headIndex == 1, 'head-2': headIndex == 2 }" transition="head-slide">
    <img class="head-bg" src="../assets/head.png">
    <div class="head-li" v-touch:tap="headTouchLeft" v-bind:class="{ 'active': headIndex == 1 }"></div>
    <div class="head-li" v-touch:tap="headTouchMiddle" v-bind:class="{ 'active': headIndex == 0 }"></div>
    <div class="head-li" v-touch:tap="headTouchRight" v-bind:class="{ 'active': headIndex == 2 }"></div>
    <img class="head-left" src="../assets/fate-arrow.png">
    <img class="head-right" src="../assets/fate-arrow.png">
  </div>

  <div class="match-wheel-left" v-show="wheelShow" transition="match-wheel-left">
    <div v-show="wheelShow" transition="wheel-panel">
      <img class="wheel-panel" id="match-wheel-panel-left" v-bind:class="{ 'wheel-panel-show': wheelShow }" src="../assets/match-wheel-panel-left.png">
    </div>
    <img class="wheel-bg" src="../assets/match-wheel-left.png">
    <div class="match-wheel-label" v-show="leftLabelShow" transition="match-wheel-label">{{ headIndex == 1 ? '' : headIndex == 2 ? 'T' : '' }} {{ labels[horoLeft] }}<div>{{ dates[horoLeft] }}</div></div>
  </div>

  <div class="match-wheel-right" v-show="wheelShow" transition="match-wheel-right">
    <div v-show="wheelShow" transition="wheel-panel-reverse">
      <img class="wheel-panel" id="match-wheel-panel-right" v-bind:class="{ 'wheel-panel-show': wheelShow }" src="../assets/match-wheel-panel-right.png">
    </div>
    <img class="wheel-bg" src="../assets/match-wheel-right.png">
    <div class="match-wheel-label" v-show="rightLabelShow" transition="match-wheel-label">{{ headIndex == 1 ? '' : headIndex == 2 ? 'P' : '' }} {{ labels[horoRight] }}<div>{{ dates[horoRight] }}</div></div>
  </div>

  <div class="match-content" v-touch:swipeleft="contentLeft" v-touch:swiperight="contentRight" v-bind:class="{ 'content-1': headIndex == 1, 'content-2': headIndex == 2 }">

    <div class="btn" v-show="btnShow" transition="btn-show">
      <div class="wave"></div>
      <div class="wave"></div>
      <div class="wave"></div>
      <div class="wave"></div>
      <a v-touch:tap="goResult" v-link="{ name: 'result' , query: {'l': horoLeft, 'r': horoRight, 't': headIndex}}">
        <img v-show="!btnSelected" src="../assets/home-btn.png">
        <img v-show="btnSelected" src="../assets/home-btn-selected.png">
        <span v-show="btnShow" transition="horoline-show"></span>
      </a>
    </div>
    <div class="copyright">beyou</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      headShow: false,
      headIndex: 0,
      btnSelected: false,
      btnShow: false,
      wheelShow: false,
      leftLabelShow: false,
      rightLabelShow: false,
      labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
      dates: ['3/21-4/19', '4/20-5/20', '5/21-6/21', '6/22-7/22', '7/23-8/22', '8/23-9/22', '9/23-10/23', '10/24-11/22', '11/23-12/21', '12/22-1/19', '1/20-2/18', '2/19-3/20'],
      horoLeft: 0,
      horoRight: 0,
      rotationSnap: 30,
      rollingLeft: false,
      rollingRight: false
    }
  },
  ready () {
    this.$set('headShow', true)
    this.$set('btnShow', true)
    this.$set('wheelShow', true)
    this.$set('leftLabelShow', true)
    this.$set('rightLabelShow', true)
    var vm = this

    var rotationSnap = this.rotationSnap
    setTimeout(function () {
      var wheelLeft = document.getElementById('match-wheel-panel-left')
      var wheelRight = document.getElementById('match-wheel-panel-right')
      Draggable.create(wheelLeft, {
        type: 'rotation',
        throwProps: true,
        throwResistance: 10000,
        snap: function (endValue) {
          return Math.round(endValue / rotationSnap) * rotationSnap
        },
        onPress: function () {
          vm.$set('leftLabelShow', false)
        },
        onDrag: function () {
          vm.$set('leftLabelShow', false)
          vm.$set('rollingLeft', true)
        },
        onThrowComplete: function () {
          vm.$set('horoLeft', vm.getIndex(this.endRotation))
          vm.$set('leftLabelShow', true)
          vm.$set('rollingLeft', false)
        },
        onClick: function () {
          vm.$set('btnSelected', true)
          vm.$set('leftLabelShow', false)
          if (Math.abs(this.y) > 90) {
            vm.$set('leftLabelShow', true)
            return
          }
          var angle = Math.round(90 - this.y) - 90 - 15
          var index = Math.ceil(angle / rotationSnap)
          var rotation = Math.round(this.rotation / 30) * 30 - index * rotationSnap
          TweenMax.to(wheelLeft, 0.5, {
            rotation: rotation,
            onUpdate: function () {
              vm.$set('rollingLeft', true)
            },
            onComplete: function () {
              vm.$set('rollingLeft', false)
              vm.$set('leftLabelShow', true)
              vm.$set('horoLeft', vm.getIndex(rotation))
            }
          })
        }
      })
      Draggable.create(wheelRight, {
        type: 'rotation',
        throwProps: true,
        throwResistance: 10000,
        snap: function (endValue) {
          return Math.round(endValue / rotationSnap) * rotationSnap
        },
        onPress: function () {
          vm.$set('rightLabelShow', false)
        },
        onDrag: function () {
          vm.$set('rightLabelShow', false)
          vm.$set('rollingRight', true)
        },
        onThrowComplete: function () {
          vm.$set('horoRight', vm.getIndex(-this.endRotation))
          vm.$set('rightLabelShow', true)
          vm.$set('rollingRight', false)
        },
        onClick: function () {
          vm.$set('btnSelected', true)
          vm.$set('rightLabelShow', false)
          if (Math.abs(this.y) < 90) {
            vm.$set('rightLabelShow', true)
            return
          }
          var angle = -(Math.round(this.y - 90) - 90 + 15)
          var index = Math.ceil(angle / rotationSnap)
          if (index > 8) {
            index = index - 12
          }
          var rotation = Math.round(this.rotation / 30) * 30 - index * rotationSnap
          TweenMax.to(wheelRight, 0.5, {
            rotation: rotation,
            onUpdate: function () {
              vm.$set('rollingRight', true)
            },
            onComplete: function () {
              vm.$set('rollingRight', false)
              vm.$set('rightLabelShow', true)
              vm.$set('horoRight', vm.getIndex(-rotation))
            }
          })
        }
      })
    }, 500)
  },
  methods: {
    headTouchLeft () {
      this.$set('headIndex', 1)
    },
    headTouchMiddle () {
      this.$set('headIndex', 0)
    },
    headTouchRight () {
      this.$set('headIndex', 2)
    },
    contentLeft () {
      switch (this.headIndex) {
        case 1:
          this.$set('headIndex', 0)
          break
        case 0:
          this.$set('headIndex', 2)
          break
        default:
      }
    },
    contentRight () {
      switch (this.headIndex) {
        case 2:
          this.$set('headIndex', 0)
          break
        case 0:
          this.$set('headIndex', 1)
          break
        default:
      }
    },
    goResult () {
      this.$set('btnSelected', true)
    },
    getIndex (rotation) {
      var rotationSnap = this.rotationSnap
      var index = Math.floor((360 - ((-rotation - rotationSnap / 2) % 360 + 360) % 360) / rotationSnap)
      return index
    }
  }
}
</script>

<style>
  .match-content {
    height: 100%;
    width: 100%;
  }

  .match-wheel-left, .match-wheel-right {
    width: 287px;
    height: 287px;
    border-radius: 100%;
    position: absolute;
    top: 30px;
  }

  .match-wheel-left {
    left: -140px;
  }

  .match-wheel-right {
    right: -140px;
  }

  .match-wheel-left-transition, .match-wheel-right-transition {
    transition: transform 0.5s ease;
  }

  .match-wheel-left-enter, .match-wheel-left-leave {
    transform: translateX(-147px);
  }

  .match-wheel-right-enter, .match-wheel-right-leave {
    transform: translateX(147px);
  }

  .match-wheel-label {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -15px;
    margin-left: 3px;
    margin-right: 3px;
    font-size: 14px;
  }

  .match-wheel-right .match-wheel-label {
    left: auto;
    right: 50%;
  }

  .match-wheel-label-transition {
    transition: opacity 0.5s ease;
    opacity: 1;
  }

  .match-wheel-label-enter, .match-wheel-label-leave {
    opacity: 0;
  }

  .match-wheel-label>div {
    font-size: 10px;
    color: #9b94af;
  }
</style>



