<template>
  <div class="match-result" id="result">
    <div v-show="show" class="content-head" transition="match-result">
      <div class="match-icon-left">
        <img v-show="horoLeft==11" src="../assets/icon11.png">
        <img v-show="horoLeft==10" src="../assets/icon10.png">
        <img v-show="horoLeft==9" src="../assets/icon9.png">
        <img v-show="horoLeft==8" src="../assets/icon8.png">
        <img v-show="horoLeft==7" src="../assets/icon7.png">
        <img v-show="horoLeft==6" src="../assets/icon6.png">
        <img v-show="horoLeft==5" src="../assets/icon5.png">
        <img v-show="horoLeft==4" src="../assets/icon4.png">
        <img v-show="horoLeft==3" src="../assets/icon3.png">
        <img v-show="horoLeft==2" src="../assets/icon2.png">
        <img v-show="horoLeft==1" src="../assets/icon1.png">
        <img v-show="horoLeft==0" src="../assets/icon0.png">
        <span>{{ labels[horoLeft] }}</span>
      </div>
      <div class="match-icon-right">
        <img v-show="horoRight==11" src="../assets/icon11.png">
        <img v-show="horoRight==10" src="../assets/icon10.png">
        <img v-show="horoRight==9" src="../assets/icon9.png">
        <img v-show="horoRight==8" src="../assets/icon8.png">
        <img v-show="horoRight==7" src="../assets/icon7.png">
        <img v-show="horoRight==6" src="../assets/icon6.png">
        <img v-show="horoRight==5" src="../assets/icon5.png">
        <img v-show="horoRight==4" src="../assets/icon4.png">
        <img v-show="horoRight==3" src="../assets/icon3.png">
        <img v-show="horoRight==2" src="../assets/icon2.png">
        <img v-show="horoRight==1" src="../assets/icon1.png">
        <img v-show="horoRight==0" src="../assets/icon0.png">
        <span>{{ labels[horoRight] }}</span>
      </div>
      <div class="match-heart" v-bind:style="{animationDuration: ( 150 - rate ) / 100 + 's'}">
        <img src="../assets/match-heart.png">
        <div class="match-rate">{{ rate + '%' }}</div>
      </div>
      <div class="heart-beat"></div>
      <div class="content-head-bottom"></div>
      <img class="corner-left" src="../assets/border-corner-c.png">
      <img class="corner-right" src="../assets/border-corner-c.png">
      <img class="middle-left" src="../assets/border-knot.png">
      <img class="middle-right" src="../assets/border-knot.png">
      <div class="content-head-topline-left"></div>
      <div class="content-head-topline-right"></div>
      <div class="content-head-middleline-left"></div>
      <div class="content-head-middleline-right"></div>
    </div>

    <div v-show="show" class="content-body" transition="match-result">
      <div class="match-star"><span></span><div v-bind:style="{width: stars[0] * 20 + '%'}"></div></div>
      <div class="match-star"><span></span><div v-bind:style="{width: stars[1] * 20 + '%'}"></div></div>
      <div class="match-star"><span></span><div v-bind:style="{width: stars[2] * 20 + '%'}"></div></div>
      <div class="match-star"><span></span><div v-bind:style="{width: stars[3] * 20 + '%'}"></div></div>
      <div class="content-body-head">
        <div v-touch:tap="contentTouchLeft" v-bind:class="{'active': contentIndex == 0}"></div>
        <div v-touch:tap="contentTouchMiddle" v-bind:class="{'active': contentIndex == 1}">{{ horoType == 1 ? '' : horoType == 2 ? 'T' : '' }}</div>
        <div v-touch:tap="contentTouchRight" v-bind:class="{'active': contentIndex == 2}">{{ horoType == 1 ? '' : horoType == 2 ? 'P' : '' }}</div>
      </div>
      <div class="match-text" v-bind:class="{'match-text-1': contentIndex == 1, 'match-text-2': contentIndex == 2}">
        <div><div v-show="contentIndex == 0" transition="match-text">{{ matchTextA }}</div></div>
        <div><div v-show="contentIndex == 1" transition="match-text">{{ matchTextL }}</div></div>
        <div><div v-show="contentIndex == 2" transition="match-text">{{ matchTextR }}</div></div>
      </div>
      <div class="content-body-border-left"></div>
      <div class="content-body-border-right"></div>
      <div class="content-scroller-bottom">
        <img class="corner-bottom-left" src="../assets/border-corner.png">
        <img class="corner-bottom-right" src="../assets/border-corner.png">
        <div></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      headIndex: 0,
      headShow: false,
      labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
      show: false,
      horoLeft: this.$route.query.l,
      horoRight: this.$route.query.r,
      horoType: this.$route.query.t,
      rate: Math.round(Math.random() * 100),
      stars: [4, 3, 5, 3],
      contentIndex: 0,
      matchTextA: '',
      matchTextL: '()',
      matchTextR: ''
    }
  },
  ready () {
    this.$set('show', true)
    Draggable.create('#result', {
      type: 'scrollTop',
      edgeResistance: 1,
      throwProps: true,
      dragClickables: true,
      lockAxis: true
    })
  },
  methods: {
    // contentLeft () {
    //   switch (this.contentIndex) {
    //     case 0:
    //       this.$set('contentIndex', 1)
    //       break
    //     case 1:
    //       this.$set('contentIndex', 2)
    //       break
    //     default:
    //   }
    // },
    // contentRight () {
    //   switch (this.contentIndex) {
    //     case 2:
    //       this.$set('contentIndex', 1)
    //       break
    //     case 1:
    //       this.$set('contentIndex', 0)
    //       break
    //     default:
    //   }
    // },
    contentTouchLeft () {
      this.$set('contentIndex', 0)
    },
    contentTouchMiddle () {
      this.$set('contentIndex', 1)
    },
    contentTouchRight () {
      this.$set('contentIndex', 2)
    }
  }
}
</script>

<style>
  .match-result {
    width: 100%;
    height: 100%;
    font-size: 14px;
    line-height: 25px;
    color: #ece5f3;
  }

  .match-result-transition {
    transition: all 0.5s ease;
    opacity: 1;
  }

  .match-result-enter, .match-result-leave {
    opacity: 0;
  }

  .match-result>div {
    min-height: 100%;
  }

  .match-result .content-head {
    height: 136px;
  }

  .match-result .content-head-topline-left,
  .match-result .content-head-topline-right,
  .match-result .corner-left,
  .match-result .corner-right {
    top: 8px;
  }

  .match-result .content-head-topline-left {
    padding-right: 0;
  }

  .match-result .content-head-topline-right {
    padding-left: 0;
  }

  .match-result .content-body {
    padding-top: 136px;
    position: static;
    overflow: hidden;
  }

  .match-result .content-head-middleline-left, .match-result .content-head-middleline-right {
    top: 24px;
    height: 100px;
  }

  .match-result .content-body-border-left, .match-result .content-body-border-right {
    padding-top: 148px;
  }

  .match-icon-left, .match-icon-right {
    position: absolute;
    top: 52px;
    width: 38px;
    height: 38px;
    border-radius: 100%;
    background: rgba(149, 93, 188, 0.5);
  }

  .match-icon-left {
    left: 30px;
  }

  .match-icon-right {
    right: 30px;
  }

  .match-icon-left:before, .match-icon-right:before {
    content: '';
    display: block;
    width: 46px;
    height: 46px;
    margin-left: -5px;
    margin-top: -5px;
    border: 1px solid rgba(149, 93, 188, 0.5);
    border-radius: 100%;
  }

  .match-icon-left img, .match-icon-right img {
    width: 60%;
    height: 60%;
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    opacity: 0.7;
  }

  .match-icon-left span, .match-icon-right span {
    width: 100px;
    height: 15px;
    line-height: 15px;
    font-size: 12px;
  }

  .match-heart {
    position: absolute;
    width: 108px;
    height: 90px;
    left: 50%;
    top: 25px;
    margin-left: -54px;
    animation: heartbeat 1s ease infinite;
  }

  .match-rate {
    position: absolute;
    width: 100px;
    font-size: 20px;
    top: 28px;
    left: 50%;
    transform: translateX(-50%);
  }

  .match-heart img {
    width: 100%;
    height: 100%;
  }

  @keyframes heartbeat {
    0% {
      transform: scale(1);
      opacity: 0.9;
    }
    50% {
      transform: scale(1.1);
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 0.9;
    }
  }

  .heart-beat {
    width: 100%;
    height: 62px;
    position: absolute;
    top: 35px;
    padding: 0 73px;
    z-index: -1;
  }

  .heart-beat:after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-image: url(../assets/heart-beat.png);
    background-repeat: repeat-x;
    background-size: contain;
    background-position: 0 0;
  }

  .match-star {
    width: 75px;
    height: 11px;
    position: absolute;
    top: 135px;
    left: 50%;
    margin-top: 45px;
    background-image: url(../assets/match-star.png);
    background-repeat: repeat-x;
    background-size: contain;
    background-position: 0 0;
  }

  .match-star:nth-of-type(2n+1) {
    transform: translateX(-100%);
    margin-left: -8%;
  }

  .match-star:nth-of-type(2n) {
    margin-left: 8%;
  }

  .match-star:nth-of-type(3),
  .match-star:nth-of-type(4)  {
    top: 190px;
  }

  .match-star span {
    display: block;
    line-height: 15px;
    width: 95px;
    position: absolute;
    top: -20px;
    left: -10px;
  }

  .match-star div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/match-star-selected.png);
    background-repeat: repeat-x;
    background-size: contain;
    background-position: 0 0;
  }

  .content-body-head {
    width: 100%;
    height: 29px;
    margin-top: 135px;
    padding: 0 24px;
    position: relative;
  }

  .content-body-head:before {
    content: '';
    display: block;
    position: absolute;
    box-sizing: border-box;
    top: 0;
    bottom: 0;
    left: 24px;
    right: 24px;
    margin: auto;
    border-radius: 3px;
    background: #9e95b3;
    opacity: 0.3;
    z-index: -1;
  }

  .content-body-head div {
    display: inline-block;
    line-height: 29px;
    width: 32%;
    position: relative;
    color: #9e95b3;
    transition: all 0.5s ease;
  }

  .content-body-head div.active {
    color: #ece5f3;
  }

  .content-body-head div:after {
    content: '';
    display: block;
    position: absolute;
    top: 29px;
    left: 50%;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 0px solid #9e95b3;
    opacity: 0.3;
    transform: translateX(-50%);
    transition: all 0.5s ease;
  }

  .content-body-head div.active:after {
    border-top-width: 9px;
  }

  .content-body-head div:nth-of-type(1):before,
  .content-body-head div:nth-of-type(3):before {
    content: '';
    display: block;
    position: absolute;
    top: 7px;
    left: -1px;
    width: 1px;
    height: 15px;
    background: #43385e;
  }

  .content-body-head div:nth-of-type(1):before {
    left: auto;
    right: -1px;
  }

  .match-text {
    position: relative;
    width: 300%;
    min-height: 1px;
  }

  .match-text-1 {
    transform: translateX(-33.3333%);
  }

  .match-text-2 {
    transform: translateX(-66.6666%);
  }

  .match-text>div {
    width: 33.3333%;
    min-height: 1px;
    float: left;
  }

  .match-text-transition {
    width: 100%;
    padding: 20px 28px 10px;
    text-align: justify;
    text-indent: 28px;
    transition: opacity 0.2s linear;
    opacity: 1;
  }

  .match-text-enter, .match-text-leave {
    opacity: 0;
  }
</style>



