<template>
  <div class="head" v-show="headShow" transition="head-slide">
    <img class="head-bg" src="../assets/head.png">
    <h2 class="head-title" v-show="headShow" transition="head-title-slide">{{ headTitle }}</h3>
  </div>

  <div class="wheel" v-show="wheelShow" transition="wheel-slide">
    <div v-show="wheelShow" transition="wheel-panel">
      <img class="wheel-panel" id="home-wheel-panel" v-bind:class="{ 'wheel-panel-show': wheelShow }" src="../assets/home-wheel-panel.png">
    </div>
    <img class="wheel-bg" src="../assets/home-wheel-bg.png">
  </div>

  <div class="horoline" v-show="horolineShow" transition="horoline-show">
    <img v-show="selectedIndex===11" src="../assets/horoline11.png">
    <img v-show="selectedIndex===10" src="../assets/horoline10.png">
    <img v-show="selectedIndex===9" src="../assets/horoline9.png">
    <img v-show="selectedIndex===8" src="../assets/horoline8.png">
    <img v-show="selectedIndex===7" src="../assets/horoline7.png">
    <img v-show="selectedIndex===6" src="../assets/horoline6.png">
    <img v-show="selectedIndex===5" src="../assets/horoline5.png">
    <img v-show="selectedIndex===4" src="../assets/horoline4.png">
    <img v-show="selectedIndex===3" src="../assets/horoline3.png">
    <img v-show="selectedIndex===2" src="../assets/horoline2.png">
    <img v-show="selectedIndex===1" src="../assets/horoline1.png">
    <img v-show="selectedIndex===0" src="../assets/horoline0.png">
  </div>

  <div class="btn" v-show="btnShow" transition="btn-show">
    <div class="wave"></div>
    <div class="wave"></div>
    <div class="wave"></div>
    <div class="wave"></div>
    <a v-touch:tap="goFate" v-link="{ name: 'fate' , params: {'horoscope': selectedIndex}}">
      <img v-show="!btnSelected" src="../assets/home-btn.png">
      <img v-show="btnSelected" src="../assets/home-btn-selected.png">
      <span v-show="horolineShow" transition="horoline-show">{{ horoscope }}</span>
    </a>
  </div>
</template>

<script>
export default {
  data () {
    return {
      headShow: false,
      headTitle: '',
      wheelShow: false,
      horolineShow: false,
      btnShow: false,
      btnSelected: false,
      rolling: false,
      labels: ['', '', '', '', '', '', '', '', '', '', '', ''],
      rotationSnap: 30,
      selectedIndex: 0
    }
  },
  computed: {
    horoscope () {
      return this.labels[this.selectedIndex]
    }
  },
  ready () {
    this.$set('headShow', true)
    this.$set('wheelShow', true)
    this.$set('horolineShow', true)
    this.$set('btnShow', true)
    var vm = this
    var wheel = document.getElementById('home-wheel-panel')
    var rotationSnap = this.rotationSnap
    setTimeout(function () {
      Draggable.create(wheel, {
        type: 'rotation',
        throwProps: true,
        throwResistance: 10000,
        snap: function (endValue) {
          return Math.round(endValue / rotationSnap) * rotationSnap
        },
        onPress: function () {
          vm.$set('horolineShow', false)
        },
        onDrag: function () {
          vm.$set('horolineShow', false)
          vm.$set('rolling', true)
        },
        onThrowComplete: function () {
          vm.$set('selectedIndex', vm.getIndex(this.endRotation))
          vm.$set('horolineShow', true)
          vm.$set('rolling', false)
        },
        onClick: function () {
          vm.$set('btnSelected', true)
          vm.$set('horolineShow', false)
          if (this.y > 0) {
            vm.$set('horolineShow', true)
            return
          }
          var angle = Math.round(Math.abs(this.y)) - 90 - 15
          var index = Math.ceil(angle / rotationSnap)
          var rotation = Math.round(this.rotation / 30) * 30 - index * rotationSnap
          TweenMax.to(wheel, 0.5, {
            rotation: rotation,
            onUpdate: function () {
              vm.$set('rolling', true)
            },
            onComplete: function () {
              vm.$set('rolling', false)
              vm.$set('horolineShow', true)
              vm.$set('selectedIndex', vm.getIndex(rotation))
            }
          })
        }
      })
    }, 1000)
  },
  methods: {
    goFate () {
      this.$set('btnSelected', true)
    },
    getIndex (rotation) {
      var rotationSnap = this.rotationSnap
      var index = Math.floor((360 - ((-rotation - rotationSnap / 2) % 360 + 360) % 360) / rotationSnap)
      return index
    }
  }
}
</script>

<style>
  .wheel {
    box-sizing: content-box;
    width: 402px;
    height: 402px;
    position: absolute;
    left: 50%;
    top: -158px;
    margin-left: -201px;
    overflow: hidden;
  }

  .wheel-slide-transition {
    transition: transform 1s ease;
    transform: translateY(0%);
  }

  .wheel-slide-enter, .wheel-slide-leave {
    transform: translateY(-100%);
  }

  .wheel-bg {
    pointer-events: none;
    width: 100%;
    height: 100%;
    margin-top: -100%;
    display: block;
    transform: translate3d(0,0,0);
  }

  .wheel-panel {
    width: 100%;
    height: 100%;
    display: block;
    position: relative;
  }

  .wheel-panel-transition, .wheel-panel-reverse-transition {
    transition: transform 1s ease;
    transform: rotate(0deg);
  }

  .wheel-panel-enter {
    transform: rotate(-360deg);
  }

  .wheel-panel-reverse-enter {
    transform: rotate(360deg);
  }

  .horoline {
    width: 215px;
    height: 220px;
    top: 40%;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    position: absolute;
    overflow: hidden;
  }

  .horoline-show-transition {
    transition: opacity 0.5s linear;
    opacity: 1;
  }

  .horoline-show-enter, .horoline-show-leave {
    opacity: 0;
  }

  .horoline img {
    width: 100%;
    height: 100%;
  }

  .btn {
    width: 110px;
    height: 110px;
    position: absolute;
    bottom: 30px;
    left: 50%;
    margin-left: -55px;
  }

  .btn-show-transition {
    transition: opacity 0.5s ease;
    opacity: 1;
  }

  .btn-show-enter, .btn-show-leave {
    opacity: 0;
  }

  .btn .wave {
    display: block;
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 100%;
    top: 50%;
    left: 50%;
    margin: -50px auto auto -50px;
    opacity: 0;
    background: #6b50a1;
    animation: wave 6s linear infinite;
  }

  .btn .wave:nth-of-type(1) {
    animation-delay: 0.5s;
  }
  .btn .wave:nth-of-type(2) {
    animation-delay: 2s;
  }
  .btn .wave:nth-of-type(3) {
    animation-delay: 3.5s;
  }
  .btn .wave:nth-of-type(4) {
    animation-delay: 5s;
  }

  @keyframes wave {
    0% {
      transform: scale(1);
      opacity: 0.15;
    }
    100% {
      transform: scale(4);
      opacity: 0;
    }
  }

  .btn a {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
  }

  .btn span {
    z-index: 3;
    line-height: 100%;
    position: absolute;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    color: #ece5f3;
    font-size: 18px;
    letter-spacing: 1px;
  }

  .btn img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  .cache-pool {
    position: absolute;
    visibility: hidden;
    top: -100000px;
  }
</style>



