import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import VueTouch from 'vue-touch'

import App from './App'
import Home from './components/Home'
import Fate from './components/Fate'
import Match from './components/Match'
import Luck from './components/Luck'
import Result from './components/Result'

Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(VueTouch)

var router = new VueRouter({
  history: false
})

router.map({
  '/': {
    component: Home
  },
  '/Fate/:horoscope': {
    name: 'fate',
    component: Fate
  },
  '/Match': {
    name: 'match',
    component: Match
  },
  '/Luck': {
    name: 'luck',
    component: Luck
  },
  '/Result': {
    name: 'result',
    component: Result
  }
})
router.start(App, 'app')



