<template>
  <div class="head" v-show="headShow" transition="head-slide">
    <img class="head-bg" src="../assets/head.png">
    <h2 class="head-title" v-show="headShow" transition="head-title-slide">{{ headTitle }}</h3>
  </div>

  <div class="luck-container">
    <div class="match-result luck" v-show="show" transition="match-result">
      <div class="content-head">
        <img class="corner-left" src="../assets/border-corner-c.png">
        <img class="corner-right" src="../assets/border-corner-c.png">
        <img class="middle-left" src="../assets/border-knot.png">
        <img class="middle-right" src="../assets/border-knot.png">
        <div class="content-head-topline-left"></div>
        <div class="content-head-topline-right"></div>
        <div class="content-head-middleline-left"></div>
        <div class="content-head-middleline-right"></div>
      </div>

      <div class="content-body">
        <div class="content-body-border-left"></div>
        <div class="content-body-border-right"></div>
        <div class="content-scroller-bottom">
          <img class="corner-bottom-left" src="../assets/border-corner.png">
          <img class="corner-bottom-right" src="../assets/border-corner.png">
          <div></div>
        </div>
        <div class="cards-stack" v-touch:swipeUp="stackUp" v-on:touchStart="stackTouchStart" v-on:touchMove="stackMove" v-bind:class="{'expand': stackShow}">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
          <img src="../assets/card-back.png">
        </div>
        <div class="card-face" v-show="cardShow" transition="card-face">
          <img src="../assets/card-face.png">
          <div>{{ text }}</div>
        </div>
        <div class="guide" v-bind:class="{'expand': stackShow}">{{ guide ? '' : ''}}</div>
        <div v-show="warning" transition="warning"><div></div></div>
        <div class="copyright"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false,
      headShow: false,
      headTitle: '',
      stackShow: false,
      cardShow: false,
      warning: false,
      text: ''
    }
  },
  ready () {
    this.$set('show', true)
    this.$set('headShow', true)
    var vm = this
    setTimeout(function () {
      vm.$set('stackShow', true)
    }, 200)
  },
  methods: {
    stackTouchStart () {
      this.$set('guide', true)
    },
    stackMove (e) {
      console.log(e)
    },
    stackUp () {
      if (this.guide) {
        this.$set('headShow', false)
        this.$set('stackShow', false)
        this.$set('cardShow', true)
      }
    }
  }
}
</script>

<style>
  .luck-container {
    height: 100%;
  }

  .match-result.luck {
    perspective: 1000px;
  }

  .match-result.luck>div {
    min-height: 0;
  }

  .match-result.luck>div.content-body {
    min-height: 100%;
  }

  .match-result.luck .copyright {
    bottom: 15px;
  }

  .cards-stack img {
    width: 81px;
    height: 134px;
    position: absolute;
    left: 50%;
    margin-left: -40px;
    transition: all 0.5s ease;
  }

  .cards-stack {
    position: absolute;
    width: 100%;
    height: 200px;
    top: 24%;
    opacity: 0;
    transition: all 0.5s ease;
    transform: translateY(100px);
  }

  .cards-stack.expand{
    opacity: 1;
    transform: translateY(0%);
  }

  .cards-stack.expand img:nth-of-type(1) {
    transform: rotate(-28deg) translate3d(-75px, -25px, 0);
  }

  .cards-stack.expand img:nth-of-type(2) {
    transform: rotate(-21deg) translate3d(-58px, -16px, 0);
  }

  .cards-stack.expand img:nth-of-type(3) {
    transform: rotate(-14deg) translate3d(-40px, -9px, 0);
  }

  .cards-stack.expand img:nth-of-type(4) {
    transform: rotate(-7deg) translate3d(-20px, -4px, 0);
  }

  .cards-stack.expand img:nth-of-type(5) {
    transform: rotate(-1deg) translate3d(0px, -4px, 0);
  }

  .cards-stack.expand img:nth-of-type(9) {
    transform: rotate(28deg) translate3d(77px, -21px, 0);
  }

  .cards-stack.expand img:nth-of-type(8) {
    transform: rotate(21deg) translate3d(59px, -14px, 0);
  }

  .cards-stack.expand img:nth-of-type(7) {
    transform: rotate(14deg) translate3d(40px, -8px, 0);
  }

  .cards-stack.expand img:nth-of-type(6) {
    transform: rotate(7deg) translate3d(20px, -4px, 0);
  }

  .guide {
    font-size: 14px;
    color: #877d9b;
    position: absolute;
    width: 100%;
    text-align: center;
    top: 24%;
    margin-top: 170px;
    opacity: 0;
    transition: opacity 1s ease;
  }

  .guide.expand {
    opacity: 1;
  }

  .card-face {
    position: absolute;
    width: 265px;
    height: 344px;
    top: 50%;
    left: 50%;
    margin-left: -132px;
    margin-top: -177px;
    z-index: 1;
  }

  .card-face img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .card-face div {
    color: #7d53b5;
    font-size: 15px;
    text-indent: 30px;
    position: absolute;
    padding: 0 30px;
    top: 50%;
    margin: auto;
    width: 100%;
    text-align: justify;
    transform: translateY(-50%);
  }

  .warning-transition {
    position: absolute;
    width: 100%;
    height: 50%;
    top: 50%;
    padding-top: 170px;
    transition: opacity 0.5s ease;
    opacity: 1;
  }

  .warning-transition div {
    position: relative;
    height: 100%;
  }

  .warning-transition div:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 14px;
    color: #877d9b;
    margin: auto;
    transform: translateY(-150%);
  }

  .warning-enter, .warning-leave {
    opacity: 0;
  }
</style>



